<template lang="pug">
  
  .note-item
    .calls__delete(v-if='isEdit' @click.stop='deleteNote(note.time)')

    .note-item__wrapper
      h1.note-item__name {{note.name}}
      .note-item__info
        p.note-item__time {{getNoteTime(note.time)}}
        p.note-item__text {{note.text}}

</template>

<script>
  
  import moment from 'moment'

  export default {
    props: {
      isEdit: Boolean,
      note: Object
    },
    methods: {
      getNoteTime(time) {
        return moment(time).format('LT');
      },
      deleteNote(time) {
        this.$store.commit('DELETE_NOTE', time)
        this.$alert.show('Заметка удалена')
      }
    }
  
  }

</script>
