<template lang="pug">
  
  .notes.full-screen
    .header
      .nav
        button(@click='edit').nav__option {{ isEdit ? "Готово" : "Изменить" }}

      h1.header__title Заметки

      .header-search
        svg.header-search__icon
          use(xlink:href='#Search')
        input(
          type='text' 
          placeholder='Поиск по заголовку'
          v-model='search'
          ).header-search__input

    .notes-list
      p(v-if='!filteredNotesList.length').phone__info Нет заметок
      NotesItem(
        v-else
        v-for='(item, index) in filteredNotesList' 
        :key='index' :note='item' :isEdit='isEdit'
        @click.native='goToNote(item.time)')

    .notes-footer
      svg.notes-footer__write(@click='createNote')
        use(xlink:href='#typeMessage')
      p.notes-footer__total Всего заметок: {{notesLength}}
  

</template>

<script>

  import {mapState, mapGetters} from 'vuex'

  import NotesItem from '@/components/Notes/NotesItem'

  export default {
    components: { NotesItem },
    data() {
      return {isEdit: false, search: ''}
    },
    computed: {
      ...mapGetters({
        notesLength: 'GET_NOTES_LIST_LENGTH'
      }),
      ...mapState({
        notesList: state => state.notes.notesList
      }),
      filteredNotesList() {
        let notes = [...this.notesList]

        notes.sort((a,b) => b.time - a.time)

        return notes.filter(({name}) => name.toLowerCase().includes(this.search.toLowerCase()))
      }
    },
    methods: {
      goToNote(time) {
        this.$router.push({name: 'Note', params: {time: time}})
      },
      createNote() {
        this.$router.push({name: 'Note', params: {time: 0}})
      },
      edit() {
        this.isEdit = !this.isEdit
      }
    },
    created() {
      this.$store.commit('REFRESH_SYSTEM_STYLE', true)
    }
  }

</script>

<style lang="sass"> @import '@/assets/sass/phone/apps/notes/main' </style>